import React, { useState, useRef, useCallback, useEffect } from "react";
import { useNavigate, useNavigation } from "react-router-dom";

const EsgTitle = ({ title, sub_title }) => {

    
    const navigation = useNavigate();
    
    return (

        <div className="div_esg" onClick={()=>{

            console.log("JK","CLICK");
            navigation("EsgMain");

        }} >
            <p>
                <h1 className="esg_title">{title}</h1> 
                <br />
                <h2 className="esg_title_sub">{sub_title}</h2>
            </p>

        </div>
      
    )
    
  };
  
  export default EsgTitle;
  