import React, { useState, useRef, useCallback, useEffect } from "react";
import { useNavigate, useNavigation } from "react-router-dom";

const PartnerTitle = ({ title, sub_title }) => {
    
    const navigation = useNavigate();
    
    return (

        <div className="dev_partner" onClick={()=>{

            console.log("JK","CLICK");
            navigation("PartnerMain");
        
        }} >
            <p>
                <h1 className="partner_title">{title}</h1> 
                <br />
                <h2 className="partner_title_sub">{sub_title}</h2>
            </p>
        
        </div>
       
    )
    
  };
  
  export default PartnerTitle;
  