import { UserRoundX } from 'lucide-react';
import Offcanvas from "react-bootstrap/Offcanvas";
import React, { useEffect, useState } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import { useTranslation } from "react-i18next";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import lang from "../assets/icon/lang.png"


const UserProfile = () => {

  const { t, i18n } = useTranslation(); // 3. useTranslation hook  



  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  return (
  
  <div className="register1" width="1000">
       
        <div className="hidden1" >
        <Dropdown style={{backgroundColor:"transparent", border:"null"}}>              
          <Dropdown.Toggle variant="success" id="dropdown-basic" style={{backgroundColor:"transparent", border:"0px"}}>
            <img src={lang} alt="lang" style={{width:"24px", height:"24px"}} />
          </Dropdown.Toggle>
          <Dropdown.Menu>
            <Dropdown.Item  onClick={()=>{
              i18n.changeLanguage('ko');

              window.scrollTo({top:0,behavior:"instant"});
            }} >
            </Dropdown.Item>
            <Dropdown.Item              
              onClick={()=>{
                i18n.changeLanguage('en');
                window.scrollTo({top:0,behavior:"instant"});
              }} >
  
              ENGLISH
            </Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </div>    

  </div>
  );
};

export default UserProfile;