import React from 'react';
import { Link } from 'react-router-dom';
import { useNavigate, useNavigation } from "react-router-dom";

const DropdownContent = ({
  submenuscontent,
  setIsDrawerOpen,
  handleToggle,
  handleFouse,
  handleClick,
}) => {

  const navigation = useNavigate();
  
  

  return (
    <div className="dropdown_content"  >
  

      {submenuscontent.map((item, index) => (

        <React.Fragment key={index} >
          
          <section>
            <div onMouseOver={() =>{

              console.log("MOUSE","2DEPT MOUSE IN " + item);

              }

              }

              onMouseOut={() =>{

                console.log("MOUSE","2DEPT MOUSE OUT " + item);
  
                }
  
                }
              onClick={()=>{

                  console.log("JK",item.submenu[0].href);
                  console.log("MOUSE","2DEPT Clicked JK" + item.submenu[0].href);
                  navigation(item.submenu[0].href);
                  window.scrollTo({top:0,behavior:"instant"});

                  setIsDrawerOpen && setIsDrawerOpen(false);
                  handleClick();     

            }}>

            <h4>{item.heading}</h4>
            <ul>
              {item.submenu.map(({ label, href }, index) => (
                <li
                  key={index}
                  onClick={() => {


                    setIsDrawerOpen && setIsDrawerOpen(false);
                    handleClick();

                  }}
                >
                  <Link to={href}>{label}</Link>
                </li>
              ))}
            </ul>
            </div>

          </section>
        </React.Fragment>
      ))}
    </div>
  );
};

export default DropdownContent;