import React, { useState, useRef, useCallback, useEffect } from "react";
import { useTranslation } from "react-i18next";
import i18n from '../lang/i18n';

import Hero from '../components/hero/Hero';
import HeroContent from '../components/hero/HeroContent';
import HeroTitle from '../components/hero/HeroTitle';

import Company from '../components/hero/Company';
import CompanyContent from '../components/hero/CompanyContent';
import CompanyTitle from '../components/hero/CompanyTitle';

import Product from '../components/hero/Product';
import ProductContent from '../components/hero/ProductContent';
import ProductTitle from '../components/hero/ProductTitle';

import Partner from '../components/hero/Partner';
import PartnerContent from '../components/hero/PartnerContent';
import PartnerTitle from '../components/hero/PartnerTitle';

import Esg from '../components/hero/Esg';
import EsgContent from '../components/hero/EsgContent';
import EsgTitle from '../components/hero/EsgTitle';

import Customer from '../components/hero/Customer';
import CustomerContent from '../components/hero/CustomerContent';
import CustomerTitle from '../components/hero/CustomerTitle';

import image from '../assets/images/home_banner.jpg';

const MainPage = () => {

 
  const { t, i18n } = useTranslation(); // 3. useTranslation hook  
 

  return (
    <dev>

      <Company>
        <CompanyContent imageSrc={image}>
          <CompanyTitle title={t('company_main_title')} sub_title={t('company_main_sub_title')}  />
        </CompanyContent>
      </Company>
      <Product>
        <ProductContent imageSrc={image}>
          <ProductTitle title={t('product_main_title')} sub_title={t('product_main_sub_title')}  />
        </ProductContent>
      </Product>
      <Partner>
        <PartnerContent imageSrc={image}>
          <PartnerTitle title={t('partner_main_title')} sub_title={t('partner_main_sub_title')}  />
        </PartnerContent>
      </Partner>
      <Esg>
        <EsgContent imageSrc={image}>
          <EsgTitle title={t('esg_main_title')} sub_title={t('esg_main_sub_title')}  />
        </EsgContent>
      </Esg>
      <Customer>
        <CustomerContent imageSrc={image}>
          <CustomerTitle title={t('customer_main_title')} sub_title={t('customer_main_sub_title')}  />
        </CustomerContent>
      </Customer>
    </dev>
   
  );
};

export default MainPage;