//import  menuData  from '../menuData';
import MenuItem from './MenuItems';
import { useTranslation } from "react-i18next";
import i18n from '../lang/i18n';

const MegaMenu = ({ handleToggle, clicked, setIsDrawerOpen }) => {
  

  const { t, i18n } = useTranslation(); // 3. useTranslation hook  

  const i18next = useTranslation();



  const menuData = [
    {
      label: i18next.t("menu_nav_company"),
      href: '/CompanyMain',
      children: [
        {
          heading: i18next.t("menu_nav_company_greeting"),
          submenu: [
            {
              label: '',
              href: '/Company_About',
            },
          ],
        },
        {
          heading: i18next.t("menu_nav_company_vision"),
          submenu: [
            { label: '', href: '/Company_Vision' },
          ],
        },
        {
          heading: i18next.t("menu_nav_company_history"),
          submenu: [
            {
              label:
                '',
              href: '/Company_History',
            },
          ],
        },
        {
          heading: i18next.t("menu_nav_company_ci"),
          submenu: [
            { label: '', href: '/Company_CI' },
          ],
        },
      ],
    },
    {
      label: i18next.t("menu_nav_product"),
      href: '/ProductMain',
      children: [
        {
          heading: i18next.t("menu_nav_product_ai"),
          submenu: [
            { label: '', href: 'Product_Kiosk' },
          ],
        },
        {
          heading: i18next.t("menu_nav_product_app"),
          submenu: [
            { label: '', href: 'Product_App' },
          ],
        },
        {
          heading: i18next.t("menu_nav_product_buy"),
          submenu: [
            { label: '', href: 'Product_Buy' },
          ],
        },
      ],
    },
    {
      label: i18next.t("menu_nav_partner"),
      href: '/PartnerMain',
      children: [
        {
          heading: i18next.t("menu_nav_partner_point"),
          submenu: [
            { label: '', href: 'Partner_Point' },
          ],
        },
        {
          heading: i18next.t("menu_nav_partner_enviroment"),
          submenu: [
            {
              label: '',
              href: 'Partner_Enviroment',
            },
          ],
        },
        {
          heading: i18next.t("menu_nav_partner_contact"),
          submenu: [
            {
              label:
                '',
              href: 'Partner_Contact',
            },
          ],
        },
      ],
    },
    {
      label: i18next.t("menu_nav_esg"),
      href: '/EsgMain',
      children: [
        {
          heading: i18next.t("menu_nav_esg_goal"),
          submenu: [
            { label: '', href: 'Esg_Goal' },
          ],
        },
        {
          heading: i18next.t("menu_nav_esg_enviroment"),
          submenu: [
            { label: '', href: 'Esg_Enviroment' },
          ],
        },
        {
          heading: i18next.t("menu_nav_esg_social"),
          submenu: [
            { label: '', href: 'Esg_Social' },
          ],
        },
        {
            heading: i18next.t("menu_nav_esg_governance"),
            submenu: [
              { label: '', href: 'Esg_Governance' },
            ],
          },
  
      ],
    },
    {
        label: i18next.t("menu_nav_customer"),
        href: '/CustomerMain',
        children: [
          {
            heading: i18next.t("menu_nav_customer_faq"),
            submenu: [
              { label: '', href: 'Customer_Faq' },
            ],
          },
          {
            heading: i18next.t("menu_nav_customer_one"),
            submenu: [
              { label: '', href: 'Customer_One' },
            ],
          },
          {
            heading: i18next.t("menu_nav_customer_as"),
            submenu: [
              { label: '', href: 'Customer_As' },
            ],
          },
          {
            heading: i18next.t("menu_nav_customer_menual"),
            submenu: [
              { label: '', href: 'Customer_Menual' },
            ],
          },
        ],
      }
  ];
  

  
  return (
    <div className="nav__container">
      <nav>
        <ul>
          {menuData.map(({ label, href, children }, index) => {
            return (
              <MenuItem
                key={index}
                {...{
                  label,
                  href,
                  children,
                  setIsDrawerOpen,
                }}
                onToggle={() => handleToggle && handleToggle(index)}
                active={clicked === index}
              />
            );
          })}
        </ul>
      </nav>
    </div>
  );
};

export default MegaMenu;